.logos {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bolder;
  width: 5vmin;
  height: 5vmin;
  margin: 1rem;
  box-shadow: -3px -3px 12px #224439;
  font-family: "Unlock", cursive;
  font-size: 1.5vmin;
  color: #7db08c;
  text-shadow: 4px 4px 12px rgb(12, 44, 2), -3px -3px 12px rgb(12, 44, 2);
  position: absolute;
  top: 5px;
  left: 30px;
  border-bottom: 4px solid rgb(0, 120, 240);
  border-left: 2px solid rgb(0, 120, 240);
  border-right: 2px solid rgb(0, 120, 240);
  border-radius: 100%;
  background: radial-gradient(#9ebd13 20%, #008552 70%);
  transition: all 1s ease-out;
  opacity: 0.7;
}

.logos:hover {
  color: #59b876;
  opacity: 1;
  font-size: 2vmin;
}

