.bg-ingredient {
	background: linear-gradient(
	  rgba(0, 0, 0, 0.2) 0%, 
	  rgba(0, 0, 0, 0.3) 45%, 
	  rgba(0, 0, 0, 0.3) 65%, 
	  rgba(0, 0, 0, 0.2) 100%), 
	url("../../assets/ingredient.jpg");
	background-size: cover;
  }
  
  .bg-randoMeal {
	background: linear-gradient(
	  rgba(0, 0, 0, 0.2) 0%, 
	  rgba(0, 0, 0, 0.1) 45%, 
	  rgba(0, 0, 0, 0.1) 65%, 
	  rgba(0, 0, 0, 0.2) 100%), 
	url("../../assets/randoMeal.jpg");
	background-size: cover;
  }
  
  .bg-minerals {
	background: linear-gradient(
	  rgba(0, 0, 0, 0.2) 0%, 
	  rgba(0, 0, 0, 0.1) 45%, 
	  rgba(0, 0, 0, 0.1) 65%, 
	  rgba(0, 0, 0, 0.2) 100%), 
	url("../../assets/minerals.jpg");
	background-size: 100vmax;
  }
  
  .bg-mealPlans {
	background: linear-gradient(
	  rgba(0, 0, 0, 0.2) 0%, 
	  rgba(0, 0, 0, 0.1) 45%, 
	  rgba(0, 0, 0, 0.1) 65%, 
	  rgba(0, 0, 0, 0.2) 100%), 
	url("../../assets/mealPlans.jpg");
	background-size: 60vw;
	background-repeat: no-repeat;
	background-position: center;
  }