@keyframes dissolve {
	0% {
	  opacity: 0;
	}
	25% {
	  opacity: 0;
	}
	75% {
	  opacity: 0;
	}
	75.1% {
	  opacity: 1;
	}
	100% {
	  opacity: 1;
	}
  }
  
  @keyframes glow {
	0% {
	  color: rgb(212, 77, 16);
	  text-shadow: -1px 1px 36px rgba(212, 157, 16, 0.7), 1px -1px 36px rgba(212, 157, 16, 0.7);
	}
	30% {
	  color: rgb(212, 97, 16);
	  text-shadow: -1px 1px 27px rgba(212, 157, 16, 0.7), 1px -1px 27px rgba(212, 157, 16, 0.7);
	}
	60% {
	  color: rgb(212, 117, 16);
	  text-shadow: -1px 1px 18px rgba(212, 157, 16, 0.7), 1px -1px 18px rgba(212, 157, 16, 0.7);
	}
	90% {
	  color: rgb(212, 137, 16);
	  text-shadow: -1px 1px 9px rgba(212, 157, 16, 0.7), 1px -1px 9px rgba(212, 157, 16, 0.7);
	}
	100% {
	  color: rgb(212, 157, 16);
	  text-shadow: -1px 1px 1px rgba(212, 157, 16, 0.7), 1px -1px 1px rgba(212, 157, 16, 0.7);
	}
  }
  
  @keyframes cyclone {
	0% {
	  opacity: 0.25;
	  transform: translateY(40%);
	  transform: translateX(20%);
	  font-size: 4vw;
	  text-shadow: 6px 6px 9px rgba(255, 96, 4, 0.7), -6px -6px 9px rgba(255, 96, 4, 0.7);
	}
	25% {
	  opacity: 0.5;
	  transform: translateY(-40%);
	  transform: translateX(-20%);
	  font-size: 3.8vw;
	  text-shadow: 9px 9px 9px rgba(255, 96, 4, 0.7), -9px -9px 9px rgba(255, 96, 4, 0.7);
	}
	50% {
	  opacity: 0.75;
	  transform: translateY(-40%);
	  transform: translateX(20%);
	  font-size: 3.6vw;
	  text-shadow: 6px 6px 9px rgba(255, 96, 4, 0.7), -6px -6px 9px rgba(255, 96, 4, 0.7);
	}
	75% {
	  opacity: 0.75;
	  transform: translateY(40%);
	  transform: translateX(20%);
	  font-size: 3.8vw;
	  text-shadow: 9px 9px 9px rgba(255, 96, 4, 0.7), -9px -9px 9px rgba(255, 96, 4, 0.7);
	}
	100% {
	  opacity: 1;
	  transform: translateY(0%);
	  transform: translateX(0%);
	  font-size: 4vw;
	  text-shadow: 6px 6px 9px rgba(255, 96, 4, 0.7), -6px -6px 9px rgba(255, 96, 4, 0.7);
	}
  }
  
  @keyframes bangbangbang {
	0% {
	  opacity: 0.25;
	  transform: translateY(40%);
	  transform: translateX(20%);
	  font-size: 4vw;
	  text-shadow: 6px 6px 9px rgba(255, 96, 4, 0.7), -6px -6px 9px rgba(255, 96, 4, 0.7);
	}
	25% {
	  opacity: 0.5;
	  transform: translateY(-40%);
	  transform: translateX(-20%);
	  font-size: 3.8vw;
	  text-shadow: 9px 9px 9px rgba(255, 96, 4, 0.7), -9px -9px 9px rgba(255, 96, 4, 0.7);
	}
	50% {
	  opacity: 0.75;
	  transform: translateY(-40%);
	  transform: translateX(20%);
	  font-size: 3.6vw;
	  text-shadow: 6px 6px 9px rgba(255, 96, 4, 0.7), -6px -6px 9px rgba(255, 96, 4, 0.7);
	}
	75% {
	  opacity: 0.75;
	  transform: translateY(40%);
	  transform: translateX(20%);
	  font-size: 3.8vw;
	  text-shadow: 9px 9px 9px rgba(255, 96, 4, 0.7), -9px -9px 9px rgba(255, 96, 4, 0.7);
	}
	100% {
	  opacity: 1;
	  transform: translateY(0%);
	  transform: translateX(0%);
	  font-size: 4vw;
	  text-shadow: 6px 6px 9px rgba(255, 96, 4, 0.7), -6px -6px 9px rgba(255, 96, 4, 0.7);
	}
  }
  
  @keyframes lightShow {
	0% {
	  background: radial-gradient(
	  rgba(255, 255, 255, 0.9) 10%, 
	  rgba(197, 197, 197, 0.6) 40%, 
	  rgba(128, 128, 128, 0.3) 60%, 
	  rgba(0, 0, 0, 0.3) 90%)
	}
	30% {
	  background: radial-gradient(
		rgba(255, 255, 255, 0.5) 20%, 
		rgba(197, 197, 197, 0.4) 40%, 
		rgba(128, 128, 128, 0.3) 60%, 
		rgba(0, 0, 0, 0.3) 90%)
	}
	60% {
	  background: radial-gradient(
		rgba(255, 255, 255, 0.3) 20%, 
		rgba(197, 197, 197, 0.2) 40%, 
		rgba(128, 128, 128, 0.1) 60%, 
		rgba(0, 0, 0, 0.3) 90%)
	}
	90% {
	  background: radial-gradient(
		rgba(255, 255, 255, 0.5) 20%, 
		rgba(197, 197, 197, 0.4) 40%, 
		rgba(128, 128, 128, 0.3) 60%, 
		rgba(0, 0, 0, 0.3) 90%)
	}
	100% {
	  background: radial-gradient(
		rgba(255, 255, 255, 0.9) 10%, 
		rgba(197, 197, 197, 0.6) 40%, 
		rgba(128, 128, 128, 0.3) 60%, 
		rgba(0, 0, 0, 0.3) 90%)
	}
  }
  
  @keyframes growString {
	from {
	  font-size: 3px;
	}
	to {
	  font-size: 2vmin;
	}
  }
  
  @keyframes expandHorizontally {
	from {
	  width: 0;
	}
	to {
	  width: 16vmin;
	}
  }
  
  @keyframes popUnder {
	from {
	  transform: translateY(-50%);
	  opacity: 0.75;
	}
	to {
	  transform: translateY(0%)
	}
  }