#cover {
  display: flex;
  margin: 0;
  padding: 1vmin;
  height: 100vh;
  width: 100%;
  justify-content: space-evenly;
  box-sizing: border-box;
  align-items: center;
  animation: dissolve 2s ease-out;
}

@keyframes dissolve {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#cover:fullscreen {
  height: 100vh;
}

.column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#center-page {
  width: 70vmin;
  justify-content: center;
}

@keyframes slideFromLeft {
  from {
    transform: translateX(-1000%);
    font-size: 0;
    opacity: 0;
  }
  to {
    transform: translateX(0%);
    opacity: 1;
    font-size: 5vmin;
  }
}

@keyframes slideFromRight {
  from {
    transform: translateX(200%);
  }
  to {
    transform: translateX(0%);
  }
}

#my-name {
  width: 4vw;
  justify-content: space-evenly;
  animation: slideFromLeft 4s ease-out;
}

#my-name, #menu, #center-page {
  height: 80vh;
  margin: 0 3vmin;
}

#first-name,
#last-name {
  color: #7db08c;
  font-size: 5vmin;
  font-family: "Unlock", cursive;
  opacity: 0.7;
  text-transform: uppercase;
  transition: all 2s ease-out;
}

#first-name {
  text-shadow: -6px -4px 4px rgb(15, 48, 60);
}

#last-name {
  text-shadow: -6px 4px 4px rgb(11, 35, 43);
}

@keyframes zoomIn {
  from {
    width: 0;
    height: 0;
    box-shadow: unset;
  }
  to {
    width: 30vmin;
    height: 30vmin;
    box-shadow: -9px 9px 36px #61C2A2, 9px -9px 36px #61C2A2;
  }
}

#profile-pic {
  background: radial-gradient(
    rgba(147, 233, 190, 0.1) 0%, 
    rgba(34, 167, 101, 0.9) 100%), 
    url("../assets/p - 270 - repeat.jpg");
  background-size: contain;
  border-top: 1px solid rgb(0, 120, 240);
  border-bottom: 1px solid rgb(0, 120, 240);
  border-left: 6px solid rgb(0, 120, 240);
  border-right: 6px solid rgb(0, 120, 240);
  background-position: center;
  border-radius: 100%;
  box-shadow: -9px 9px 36px #61C2A2, 9px -9px 36px #61C2A2;
  width: 30vmin;
  height: 30vmin;
  align-self: center;
  justify-content: center;
  transition: all 1s ease-out;
  animation: zoomIn 3s ease-out;
}

#profile-pic:hover {
  cursor: pointer;
  box-shadow: 0 27px 45px -9px #0eaee8;
}

#profile-pic:active {
  cursor: context-menu;
  opacity: 0;
}

#menu {
  justify-content: space-evenly;
  align-items: center;
  /* animation: slideFromRight 3s ease-out; */
}

@keyframes plopMenuButton1 {
  from {
    transform: translateY(-1000%);
    width: 0;
    height: 0;
    font-size: 0;
    box-shadow: unset;
  }
  to {
    width: 7.5vmin;
    height: 7.5vmin;
    font-size: 2.5vmin;
    box-shadow: 2px 2px 99px #61C2A2;
    transform: translateY(0%)
  }
}

@keyframes plopMenuButton2 {
  from {
    transform: translateY(-1400%);
    width: 0;
    height: 0;
    font-size: 0;
    box-shadow: unset;
  }
  to {
    width: 7.5vmin;
    height: 7.5vmin;
    font-size: 2.5vmin;
    box-shadow: 2px 2px 99px #61C2A2;
    transform: translateY(0%)
  }
}

@keyframes plopMenuButton3 {
  from {
    transform: translateY(-1800%);
    width: 0;
    height: 0;
    font-size: 0;
    box-shadow: unset;
  }
  to {
    width: 7.5vmin;
    height: 7.5vmin;
    font-size: 2.5vmin;
    box-shadow: 2px 2px 99px #61C2A2;
    transform: translateY(0%)
  }
}

@keyframes plopMenuButton4 {
  from {
    transform: translateY(-2200%);
    width: 0px;
    height: 0px;
    font-size: 0px;
    box-shadow: unset;
  }
  to {
    width: 7.5vmin;
    height: 7.5vmin;
    font-size: 2.5vmin;
    box-shadow: 2px 2px 99px #61C2A2;
    transform: translateY(0%)
  }
}
#skills {
  animation: plopMenuButton1 4s ease-out;
}

#projects {
  animation: plopMenuButton2 3.5s ease-out;
}

#resume {
  animation: plopMenuButton3 3s ease-out;
}

#contact {
  animation: plopMenuButton4 2.5s ease-out;
}

.menu-item {
  display: flex;
  color: unset;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 2.5vmin;
  background: radial-gradient(rgba(94, 155, 113, 0.8) 0%, rgba(204, 224, 210, 0.8) 100%);
  border: unset;
  border-bottom: 4px solid rgb(0, 120, 240);
  border-left: 2px solid rgb(0, 120, 240);
  border-right: 2px solid rgb(0, 120, 240);
  border-radius: 100%;
  box-shadow: 2px 2px 99px #61C2A2;
  width: 7.5vmin;
  height: 7.5vmin;
  transition: all 300ms ease;
}

.menu-item:hover {
  cursor: pointer;
  background: radial-gradient(rgb(94, 155, 113) 0%, rgb(204, 224, 210) 100%);
  transform: scale(1.4);
}

.menu-item:active {
  cursor: context-menu;
  transform: scale(0.8);
}

.title {
  font-size: 5vmin;
  text-shadow: -2px 2px 4px #61C2A2;
  display: flex;
  justify-content: center;
}

@keyframes slideFromBottom {
  from {
    transform: translateY(90%);
  }
  to {
    transform: translateY(0%);
  }
}

#content {
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  font-family: "Montserrat", sans-serif;
  color: rgba(204, 224, 210, 1);
  display: flex;
  flex-direction: column;
  transition: all 2s ease-in;
  text-align: center;
  line-height: 2.5vmin;
}

@keyframes revealString {
  0% {
    opacity: 0;
    color: rgba(204, 224, 210, 0.1);
  }
  30% {
    opacity: 0.3;
    color: rgba(204, 224, 210, 0.2);
  }
  60% {
    opacity: 0.6;
    color: rgba(204, 224, 210, 0.3);
  }
  90% {
    opacity: 0.8;
    color: rgba(204, 224, 210, 0.6);
  }
  100% {
    opacity: 1;
    color: rgba(204, 224, 210, 0.9);
  }
}

.intro-p, .link-item, .content-item {
  font-size: 2.5vmin;
}

.intro-p {
  animation: revealString 1s ease-in;
}

.content-item {
  color: unset;
  display: flex;
  text-shadow: -1px 2px 3px #61C2A2;
  margin: 0 1rem;
  align-self: center;
}

#content::-webkit-scrollbar {
  display: none;
}

.intro:hover {
  cursor: grab;
}

.link-item {
  color: unset;
  display: flex;
  transition: all 0.5s ease;
  align-self: center;
}

.link-item:hover {
  text-shadow: 3px 3px 7px #61C2A2, -3px -3px 7px #61C2A2;
  font-weight: bold;
}

.contact-logo {
  border: 1px solid rgb(132, 254, 148);
  padding: 0.6rem 1.4rem;
  height: 5vmin;
  display: flex;
  justify-items: center;
  border-radius: 4px;
  box-shadow: 1px 1px 4px inset rgb(132, 254, 148);
  background: linear-gradient(25deg, rgba(87, 167, 98, 0.4) 25%, rgba(87, 167, 98, 0.1));
  transition: all 1s ease;
}

.contact-logo:hover {
  box-shadow: 3px 3px 7px #61C2A2, -3px -3px 7px #61C2A2;
}

.mailtoui {
  position: relative;
}

.modalOpen {
  animation: openModal 0.5s ease-out forwards;
}

@keyframes openModal {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  50% {
    opacity: 1;
    transform: translateY(20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

