@keyframes showCover {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.75;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideX {
  0% {
    transform: translateX(-50%);
  }
  50% {
   transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes slideY {
  0% {
    transform: translateY(-100%);
    opacity: 0.5;
    font-size: 0.5vmin;
  }
  50% {
   transform: translateY(100%);
   opacity: 0.75;
   font-size: 1vmin;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}


.errands {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(45deg, rgba(2, 12, 68, 1), rgba(5, 31, 179, 1));
  height: 100vh;
  overflow: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.errands::-webkit-scrollbar {
  display: none;
}

#errands-header {
  justify-self: center;
  padding: 0;
  margin: 5px auto;
  align-self: start;
  height: 10vh;
}

#errands-header:hover {
  cursor: default;
}

@keyframes growTitle {
  from {
    font-size: 10px;
  }
  to {
    font-size: 4vmin;
  }
}

#errands-title {
  color: rgba(169, 201, 255, 1);
  text-shadow: 1px -2px 6px rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  font-family: 'Russo One', sans-serif;
  font-size: 4vmin;
  animation: growTitle 3s ease-out;
}

#cloud-main-1 {
  height: 9vmin;
  background: linear-gradient(rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.2))
}

@keyframes growWord {
  from {
    font-size: 5px;
  }
  to {
    font-size: 2.5vmin;
  }
}

#errand-category {
  text-transform: uppercase;
  font-family: "McLaren", cursive;
  cursor: ns-resize;
  font-size: 2.5vmin;
  text-shadow: 1px -2px 6px rgba(255, 255, 255, 0.7), -1px 2px 6px rgba(255, 255, 255, 0.7);
  margin: 1vmin auto;
  color: white;
  animation: growWord 3s ease-out;
}

@keyframes grow {
  from {
    height: 5px;
    width: 5px;
    opacity: 0;
  }
  to {
    height: 4vmin;
    width: 4vmin;
    opacity: 1;
  }
}

#add-errand-button {
  background: rgba(20, 178, 75, 0.6);
  border: unset;
  border-right: 4px solid rgba(5, 45, 19, 0.9);
  border-bottom: 3px solid rgba(5, 58, 23, 0.9);
  border-radius: 100%;
  font-weight: bold;
  font-size: 2vmin;
  margin: auto 1vmin;
  padding: 5px;
  transition: background 1s ease;
  animation: grow 3s ease;
}

#add-errand-button:hover {
  cursor: pointer;
  background: rgba(20, 178, 75, 0.9);
}

#add-errand-button:active {
  scale: 0.9;
  border: unset;
  border-left: 4px solid rgba(5, 45, 19, 0.9);
  border-top: 3px solid rgba(5, 58, 23, 0.9);
}


#add-icon {
  height: 4vmin;
  animation: grow 3.5s ease-in;
}

.errand-input {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: unset;
  border-bottom: 3px solid white;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  color: white;
}

#errand-input {
  display: flex;
  margin: 0.5vmin auto;
}

#errand-category-picker {
  display: flex;
  align-items: center;
}

.errand-input::placeholder {
  color: white;
  font-size: 3vmin;
}

input[type=date] {
  cursor: pointer;
  font-size: 3vmin;
  padding: unset;
  width: 22vmin;
  font-family: "Kanit", sans-serif;
  color: white;
}

::-webkit-calendar-picker-indicator {
  filter: invert(1);
}

.inp {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-top-right-radius: 3vw;
  border-top-left-radius: 3vw;
  height: 20vh;
  margin: 10px auto;
}

#scroll-icon {
  height: 4vmin;
  opacity: 0.6;
}

@keyframes expandTo {
  from {
    width: 0;
  }
  to {
    width: 36vmin
  }
}

@keyframes expandToD {
  from {
    width: 0;
  }
  to {
    width: 22vmin;
  }
}

#errand-task {
  width: 36vmin;
  font-size: 3vmin;
  margin-right: 1vmin;
}

#errand-task {
  animation: expandTo 2.5s ease-out;
}

#task-due-date {
  text-align: center;
  animation: expandToD 3s ease-out;
}

.errand-entryeven {
  color: rgba(255, 255, 255, 0.9);
}

.errand-entryodd {
  color: rgb(1, 123, 148);
}

.errand-entry-task:hover {
  cursor: pointer;
}

.outp {
  width: 80vmin;
  height: 80vh;
  overflow-y: scroll;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  font-size: 3vmin;
  line-height: 0.3vmin;
  padding: 0 10px;
}

.outp::-webkit-scrollbar {
  display: none;
}

.errand-entryodd, .errand-entryeven {
  display: flex;
  justify-content: space-between;
  padding: 0 3vmin;
  font-family: "Heebo", sans-serif;
  font-weight: bold;
  animation: slideY 1s ease-out;
}

.delete-button {
  background: linear-gradient(rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.1));
  padding: 0.5vmin;
  font-weight: bold;
  font-size: 2vmin;
  color: rgba(255, 255, 255, 0.9)
}

.delete {
  visibility: hidden;
}

.delete:hover {
  visibility: visible;
}

