@keyframes slideFromLeft {
	0% {
	  transform: translateX(-100%);
	  opacity: 0.5;
	}
	50% {
	  transform: translateX(10%);
	  opacity: 1;
	}
	100% {
	  transform: translateX(0%);
	}
  }

  @keyframes slideFromRight {
	0% {
	  transform: translateX(100%);
	  opacity: 0.5;
	}
	50% {
	  transform: translateX(-10%);
	  opacity: 1;
	}
	100% {
	  transform: translateX(0%);
	}
  }

  @keyframes slideFromTop {
	0% {
	  transform: translateY(-100%);
	  opacity: 0.5;
	}
	50% {
	  transform: translateY(10%);
	  opacity: 1;
	}
	100% {
	  transform: translateY(0%);
	}
  }

  @keyframes slideFromBottom {
	0% {
	  transform: translateY(100%);
	  opacity: 0.5;
	}
	50% {
	  transform: translateY(-10%);
	  opacity: 1;
	}
	100% {
	  transform: translateY(0%);
	}
  }

  @keyframes showCover {
	0% {
	  opacity: 0;
	}
	50% {
	  opacity: 0.5;
	}
	100% {
	  opacity: 1;
	}
  }

  @keyframes openVariableSummary {
	0% {
	  opacity: 0;
	  transform: translateY(-100%);
	}
	50% {
	  opacity: 1;
	  transform: translateY(20%);
	}
	100% {
	  opacity: 1;
	  transform: translateY(0%);
	}
  }
  
  @keyframes openCategorySummary {
	0% {
	  opacity: 0;
	  transform: translateY(-100%);
	}
	50% {
	  opacity: 0.5;
	  transform: translateY(20%);
	}
	100% {
	  opacity: 1;
	  transform: translateY(0%);
	}
  }

  @keyframes openOverview {
	0% {
	  opacity: 0;
	}
	50% {
	  opacity: 0.5;
	}
	100% {
	  opacity: 1;
	}
  }